    grid-template-columns: 1fr 2fr;
  }
}
.testimoniales-blog .testimoniales__titulo {
  font-weight: 400;
  margin-bottom: 2.4rem;
}
.testimoniales-blog .testimoniales__titulo span {
  font-weight: 700;
  display: block;
}
.testimoniales-blog .testimoniales__titulo ::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.testimoniales-blog .testimoniales__testimonial {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.4rem;
  background-color: #FFFFFF;
}
.testimoniales-blog .testimoniales__img {
  width: 25rem;
}
.testimoniales-blog .testimoniales__content {
  padding: 0 1.6rem 1.6rem;
}
@media (min-width: 768px) {
  .testimoniales-blog .testimoniales__content {
    padding: 0 3.2rem 3.2rem;
  }
}
@media (min-width: 1440px) {
  .testimoniales-blog .testimoniales__content {
    padding: 0 4.2rem 4.2rem;
  }
}
.testimoniales-blog .testimoniales__nombre {
  margin-bottom: 0.8rem;
  font-size: 2.8rem;
  font-weight: 700;
}
.testimoniales-blog .testimoniales__texto {
  text-align: center;
}

.blog__titulo {
  font-weight: 400;
  margin-bottom: 2.4rem;
}
.blog__titulo span {
  font-weight: 700;
  display: block;
}
.blog__titulo ::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.blog__grid {
  display: grid;
  gap: 2.4rem;
}
.blog__entrada {
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .blog__entrada {
    display: flex;
  }
}
.blog__content {
  padding: 1.6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 0.8rem;
}
.blog__img {
  width: 100%;
  height: 24rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .blog__img {
    width: 30rem;
    height: 100%;
  }
}
.blog__boton {
  width: 100%;
  margin-top: 1.6rem;
  padding: 1.2rem 4rem;
  border: none;
  display: inline-block;
  font-size: 1.4rem;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  background-color: #3b4862;
  transition-duration: 0.3s;
  transition-property: background-color;
}
@media (min-width: 768px) {
  .blog__boton {
    font-size: 1.6rem;
  }
}
@media (min-width: 768px) {
  .blog__boton {
    width: auto;
  }
}

.footer {
  padding-top: 4.8rem;
  background-color: #3b4862;
}
.footer__grid {
  margin-bottom: 1.6rem;
  display: grid;
  gap: 2.4rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .footer__grid {
    padding-top: 9.6rem;
    grid-template-columns: repeat(3, 1fr);
    gap: 4.8rem;
  }
}
@media (min-width: 768px) {
  .footer__menu {
    flex-direction: column;
  }
}
.footer__heading {
  margin-bottom: 1.6rem;
}
.footer__link-vertical {
  align-self: flex-start;
}
.footer__img {
  width: 25rem;
}
.footer__copyright {
  padding-bottom: 0.8rem;
  text-align: center;
